<template>
  <div>
    <div class="wrapper wrapper-content animated fadeInRight">
      <div class="row">
        <div class="col-lg-12">
          <div class="ibox float-e-margins">
            <div class="ibox-title">
              <h5>技能示例</h5>
              <button @click="testJson" type="button" class="btn btn-xs">
                Test
              </button>
            </div>
            <div class="ibox-content">
              <ul class="nav nav-pills">
                <li role="presentation" v-for="skill in allSkills"><a href="#" @click="displayExample(skill)">{{skill.name}}</a></li>
              </ul>
              <br>
              <table class="footable table table-stripped toggle-arrow-tiny">
                <thead>
                  <tr>
                    <th>{{exampleName}}:</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="example in examplesOfSkill">
                    <td>{{example.example}}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  import TransTime from "../common/time.js"

  export default {
    created: function() {
      var vm = this;
      vm.$http.get('/api/bg/skills/').then((res) => {
        this.allSkills = res.data;
        console.info(this.allSkills)
        this.exampleName = this.allSkills[0].name
      })
    },
    data() {
      return {
        skill: {},
        allSkills: [],
        exampleDisplay: '',
        exampleName: '',
        examplesOfSkill: [],
      }
    },
    methods: {
      displayExample(skill) {
        this.exampleName = skill.name;
        console.info(skill.id)
        var vm = this;
        vm.$http.get('/api/bg/skills/' + skill.id + "/skillexamples/").then((res) => {
          console.info(res)
          this.examplesOfSkill = res.data;
        })
      },
      testJson() {
        var vm = this;
        vm.$http.get('/jpush/test').then((res) => {
          console.info(res)
        })
      }
    },
    computed: {
      timeTrans: function(time) {
        return function(time) {
          return TransTime.transTimeFunc.transTime(time)
        }
      },
    }
  }
</script>

<style>
</style>



